.about-section {
    position: relative;
    width: 100%;
    margin: 0;
    min-height: 150vh;
    font-family: 'Quicksand', sans-serif;
}

.cards {
    position: absolute;
}

.first-p {
    top: 10rem;
    left: 15vw;
    width: 20rem;
    animation: movingP1 3s infinite;
}

.second-p {
    top: 20rem;
    right: 2vw;
    width: 40rem;
}

.third-p {
    top: 106vh;
    left: 20vw;
    width: 35rem;
}

.about-section svg {
    width: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
}

.first-arrow {
    position: absolute;
    width: 15%;
    transform: rotate(245deg);
}

.typing-animation {
    background-image: url(../../../public/img/tecleoCompuUno.png);
    width: 200px;
    min-height: 200px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    animation: tecleo 1s infinite;
    position: absolute;
    right: 5rem;
    top: 6rem;
}

@keyframes tecleo {
    50% {
        background-image: url(../../../public/img/tecleoCompuDos.png);
    } 
    100% {
        background-image: url(../../../public/img/tecleoCompuUno.png);
    }
}

.about-section .arrow, .about-section .arrow-two, .about-section .ricardo-milos {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    position: absolute;
}

.arrow {
    background: url(../../../public/img/arrow.gif);
    width: 11vw;
    height: 22vh;
    transform: rotate(-20deg);
    left: 40vw;
    top: 25vh;
}

.arrow-two {
    background: url(../../../public/img/flecha-dos.png);
    width: 18vw;
    height: 28vh;
    transform: rotate(140deg);
    left: 30vw;
    bottom: 53vh;
}

.ricardo-milos {
    background: url(../../../public/img/dancing.gif);
    width: 18vw;
    height: 40vh;
    right: 15vw;
    bottom: 10vh;
}