.form {
    min-height: 80vh;
    width: 100%;
    background-color: black;
    position: relative;
    color: white;
}

.form h1 {
    text-align: center;
    margin-bottom: 10vh;
    padding-top: 5vh;
    
    font-family: 'Josefin Sans', sans-serif;
    font-size: 60px;
}

.form form {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 50vw;
    gap: 5vh;
    padding-bottom: 30vh;
    margin-left: 10vw;
}

.form .astronaut {
    background: url(../../../public/img/gatoVolador.gif);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 45vw;
    height: 55vh;
    position: absolute;
    right: -7vw;
    bottom: 60vh;
}

button {
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 3vh;
    width: 200px;
    height: 80px;
    line-height: 40px;
    font-size: 20px;
    font-family: 'Josefin Sans', sans-serif;
    text-decoration: none;
    color: black;
    border: 2px solid #333;
    letter-spacing: 2px;
    text-align: center;
    transition: all .35s;
  }
  
button span {
    position: relative;
    z-index: 2;
}
  
button:after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 0;
    height: 100%;
    background: black;
    transition: all .35s;
}
  
button:hover {
    color: #fff;
}
  
button:hover:after {
    width: 100%;
}

.formState {
    opacity: 0;
    position: absolute;
    top: 5vh;
    right: 5vw;
    width: 200px;
    height: 80px;
    transition: 2s;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 25px;
}

.formState.success {
    background-color: green;
    opacity: 1;
}